body{font-family:'Short Stack', sans-serif;color:#2c3e50;font-size:15px;padding-bottom:30px}body a{color:#5fd2db;text-decoration:none;-webkit-tap-highlight-color:transparent;-webkit-tap-highlight-color:transparent;cursor:pointer}body a:active{-webkit-transform:scale3d(1.1, 1.1, 1);transform:scale3d(1.1, 1.1, 1)}body input[type=text],body input[type=text]:focus{color:#2c3e50;border:none;margin:0;padding:0;box-shadow:none;outline:initial;border-bottom:1px solid #eee}.main{padding:0 10px;max-width:400px;margin:auto}.main .login{margin:50px 0 50px 0}.main .login .firebaseui-busy-indicator{top:0}.main .login .mdl-button{font-family:'Short Stack', sans-serif}.main .login .firebaseui-idp-password,.main .login .firebaseui-idp-password:hover{background-color:#ff5959}.icon{display:inline-block;text-indent:-9999px;background-repeat:no-repeat;-webkit-tap-highlight-color:transparent;-webkit-tap-highlight-color:transparent}.tomato{width:30px;height:30px;background:url("assets/tomato.svg");background-repeat:no-repeat;opacity:1}.tomato:active{-webkit-transform:scale(1.1, 1.1);transform:scale(1.1, 1.1)}.main.start .title{max-width:280px;margin:40px auto}.main.start .footer{text-align:center;display:none}.title{text-align:left;margin:40px -10px 40px -10px;text-align:center}.title h1{color:#42b983;text-align:center;font-size:38px;display:inline-block;margin:0;line-height:100px;position:relative;top:10px}.title .logo{width:100px;height:100px;display:inline-block;vertical-align:top}.instruction{text-align:center;margin:20px 0;line-height:1.5}.instruction span{display:inline-block}.instruction.team{margin-top:80px}.footer p{margin:10px 0}.fade-transition{-webkit-transition:opacity 0.3s;transition:opacity 0.3s}.fade-enter,.fade-leave{opacity:0}



